body {
  margin: 0;
  padding: 0;
  font-family: "Noto sans JP", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}

article{
  margin-bottom: 30px;
  padding: 30px;
}

article a{
  color: #444;
}

article a:hover{
  color: #777;
}

article a:visited{
  color: #888;
}

blockquote{
  border-left: 5px solid silver;
  padding-left: 15px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", "Noto sans JP", monospace;
  line-height: 160%;
}

h1, h2, h3, h4, h5, h6{
  font-weight: normal;
}

img{
  width: 100%;
}

pre{
  background: #333;
  border: 1px solid silver;
  overflow-x: auto;
  color: #ddd;
  padding: 10px;
}

table{
  border-collapse: collapse;
}

table th{
  background: #ddd;
}

table th, table td{
  border: 1px solid #999;
  padding: 5px;
}

@media screen and (max-width: 900px){
  header{
    display: auto;
  }

  header h1{
    display: block;
    text-align: center;
  }

  header ul{
    text-align: center;
  }

  header li a{
    padding:0 15px;
  }

  .articleHeader h1{
    font-size: 2em;
  }
}

@media screen and (min-width: 900px){
  header{
    display: -webkit-flex;
    display: flex;
    height: 70px;
  }

  header h1{
    -webkit-flex-grow: 1;
            flex-grow: 1;
  }

  header li a{
    padding: 0 30px;
  }

  .articleHeader h1{
    font-size: 3em;
  }
}

.App {
  max-width: 900px;
  margin: 0 auto;
}

header h1{
  margin: 0;
  line-height: 70px;
}

header h1 a{
  text-decoration: none;
  color: black;
}

header ul{
  list-style: none;
  line-height: 100%;
  margin: 0;
  padding: 0;
  height: 70px;
}

header li{
  display: inline-block;
  height: 100%;
  line-height: 70px;
  text-align: center;
}

header ul a{
  color: black;
  text-decoration: none;
  display: block;
}

header ul a:hover{
  background: #eee;
}

header ul a:visited{
  color: black;
}


/****************************
       ArticleList
****************************/
.ArticleList{
  padding: 0 30px 30px 30px;
}

.ArticleList a{
  color: #333;
  text-decoration: none;
}

.ArticleList h2{
  text-decoration: underline;
  margin: 10px 0;
  margin-right: 10px;
  padding: 0;
  display: inline-block;
}

.ArticleListDate{
  display: inline-block;
}

.ArticleListItem{
  margin-top: 30px;
}

/****************************
          Article
****************************/
.articleHeader{
  text-align: center;
}

.articleHeader h1{
  margin-bottom: 0;
}

.content h1, .content h2{
  border-bottom: 1px solid silver;
  margin-top: 30px;
}

.content h1{
  font-size: 1.8em;
}
